@font-face {
  font-family: "Montserrat Alternates Medium";
  src: url("../fonts/MontserratAlternates-Medium.ttf") format("truetype");
  src: url("../fonts/MontserratAlternates-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/MontserratAlternates-Medium.woff") format("woff"), url("../fonts/MontserratAlternates-Medium.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat Medium";
  src: url("../fonts/Montserrat-Medium.eot");
  src: url("../fonts/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat Light";
  src: url("../fonts/Montserrat-Light.eot");
  src: url("../fonts/Montserrat-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Light.woff") format("woff"), url("../fonts/Montserrat-Light.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-style: normal;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
}

.header {
  position: relative;
}
.header__navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 10px;
  background: rgba(72, 3, 111, 0.55);
}
.header__navbar-logo {
  display: flex;
  align-items: center;
}
.header__navbar-logo-text {
  font-family: "Montserrat Alternates Medium", sans-serif;
  font-weight: 500;
  font-size: 40px;
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 5px;
}
.header__navbar-buttons {
  display: flex;
  margin-right: 50px;
}
.header__navbar-buttons .about-project, .header__navbar-buttons .copybook, .header__navbar-buttons .events {
  margin-right: 22px;
}
.header__navbar-buttons * {
  font-family: "Montserrat Medium", sans-serif;
  font-weight: 500;
  font-size: 24px;
  border-style: none;
  border-radius: 50px;
  background-color: rgb(95, 37, 128);
  color: #ffffff;
}
.header__navbar-buttons *:hover {
  cursor: pointer;
  background-color: rgba(95, 37, 128, 0.8);
}
.header__navbar-buttons .about-project, .header__navbar-buttons .leaders {
  padding: 12px;
}
.header__navbar-buttons .copybook, .header__navbar-buttons .events {
  padding: 12px 24px;
}
.header__navbar-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__navbar-menu-account, .header__navbar-menu-search, .header__navbar-menu-hamburger {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border-style: none;
  background-color: rgb(95, 37, 128);
  margin-right: 10px;
}
.header__navbar-menu-account:hover, .header__navbar-menu-search:hover, .header__navbar-menu-hamburger:hover {
  cursor: pointer;
  background-color: rgba(95, 37, 128, 0.8);
}
.header__navbar-menu-hamburger {
  flex-direction: column;
  align-items: center;
}
.header__navbar-menu-hamburger span {
  display: block;
  width: 22px;
  margin-bottom: 4px;
  height: 2.5px;
  background-color: #ffffff;
  text-align: center;
}
.header__navbar-menu-hamburger span:last-child {
  margin-left: 5px;
  width: 16px;
  margin-bottom: 0;
}
.header__navbar-hamburger-content {
  position: absolute;
  font-family: "Montserrat Medium", sans-serif;
  font-weight: 500;
  width: 80%;
  right: 0;
  z-index: 992;
  padding: 47px 0 220px 0;
  font-size: 24px;
  line-height: 30px;
  background-color: rgba(155, 117, 176, 0.76);
  border-bottom-left-radius: 75px;
  border-bottom-right-radius: 75px;
  flex-direction: column;
  display: none;
}
.header__navbar-hamburger-content-link {
  margin-left: 32px;
  text-decoration: none;
  color: #ffffff;
  margin-bottom: 15px;
  margin-top: 15px;
}
.header__navbar-hamburger-content-link:first-child {
  margin-top: 0;
}
.header__navbar-hamburger-content.is-active {
  display: flex;
}

.main {
  padding: 120px 75px 190px 75px;
  font-family: "Montserrat Light", sans-serif;
  font-weight: 300;
  line-height: 30px;
  font-size: 24px;
}
.main a {
  color: rgb(0, 0, 0);
}
.main h2 {
  font-family: "Montserrat Medium", sans-serif;
  font-weight: 500;
  font-size: 36px;
  line-height: 22px;
  margin-bottom: 30px;
}
.main .project-content__title {
  margin-bottom: 40px;
}
.main .project-content__tasks {
  margin-left: 35px;
}
.main .project-content__inspirers {
  margin-left: 35px;
}
.main .project-history {
  margin-bottom: 120px;
  margin-top: 60px;
}
.main .project-pictures__first {
  margin-right: 60px;
}
.main .project-pictures__third {
  margin-top: 66px;
  margin-bottom: 66px;
}
.main .project-pictures__sixth {
  margin-top: 95px;
}
.main .project-membership {
  margin-top: 30px;
  margin-bottom: 40px;
}
.main .project-use {
  margin-top: 40px;
}
.main .project-use__text span {
  font-weight: bold;
}
.main .project-use__use-area {
  margin-left: 35px;
}
.main .project-publishings {
  margin-top: 40px;
}
.main .project-publishings__list {
  margin-left: 30px;
}
.main .project-contacts {
  margin-top: 40px;
}
.main .project-contacts__list {
  list-style-type: none;
}

.footer {
  padding: 35px 0;
  background-color: rgb(95, 37, 128);
}
.footer .container {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.footer-logo {
  display: flex;
  align-items: center;
}
.footer-logo__text {
  font-family: "Montserrat Alternates Medium", sans-serif;
  font-weight: 500;
  font-size: 40px;
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 5px;
}
.footer__authors {
  font-family: "Montserrat Light", sans-serif;
  font-weight: 300;
  color: #ffffff;
  font-size: 20px;
  line-height: 22px;
  margin-left: 40px;
}
.footer__polytech-logo {
  display: flex;
  flex-basis: 20%;
  margin-top: 30px;
  margin-left: 15px;
}
.footer__support-text {
  font-family: "Montserrat Light", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
  margin-left: 40px;
}

@media (min-width: 1199px) and (max-width: 1399px) {
  .header__navbar-buttons {
    margin-right: 0;
  }
  .header__navbar-buttons * {
    font-size: 20px;
  }
  .header__navbar-buttons .about-project, .header__navbar-buttons .copybook, .header__navbar-buttons .events {
    margin-right: 12px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .footer .container {
    max-width: 1060px;
  }
  .footer__authors {
    margin-left: 20px;
  }
  .header__navbar {
    padding: 20px 10px;
    justify-content: space-around;
  }
  .header__navbar-logo-text {
    font-size: 35px;
  }
  .header__navbar-buttons {
    margin-right: 0;
  }
  .header__navbar-buttons * {
    font-size: 16px;
  }
  .header__navbar-buttons .about-project, .header__navbar-buttons .leaders {
    padding: 16px 10px;
  }
  .header__navbar-buttons .copybook, .header__navbar-buttons .events {
    padding: 16px 12px;
  }
  .header__navbar-buttons .about-project, .header__navbar-buttons .copybook, .header__navbar-buttons .events {
    margin-right: 5px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .header__navbar {
    padding: 20px 10px;
    justify-content: space-around;
  }
  .header__navbar-logo-text {
    font-size: 32px;
  }
  .header__navbar-buttons {
    margin-right: 0;
  }
  .header__navbar-buttons * {
    font-size: 16px;
  }
  .header__navbar-buttons .about-project, .header__navbar-buttons .leaders {
    padding: 16px 8px;
  }
  .header__navbar-buttons .copybook, .header__navbar-buttons .events {
    padding: 16px 10px;
  }
  .header__navbar-buttons .about-project, .header__navbar-buttons .copybook, .header__navbar-buttons .events {
    margin-right: 5px;
  }
  .footer .container {
    max-width: 960px;
  }
  .footer__authors {
    margin-left: 20px;
  }
}